.comics {
   &__list {
      margin-top: 45px;
   }
   &__grid {
      display: grid;
      grid-template-columns: repeat(auto-fill, 225px);
      justify-content: space-between;
      row-gap: 55px;
   }
   &__item {
      transition: 0.5s transform;
      &-img {
         box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
         width: 225px;
         height: 345px;
      }
      &-name {
         margin-top: 10px;
         font-weight: bold;
         font-size: 14px;
         line-height: 18px;
      }
      &-price {
         margin-top: 5px;
         font-weight: bold;
         font-size: 14px;
         line-height: 18px;
         color: rgba(0, 0, 0, 0.6);
         text-transform: uppercase;
      }
      &-appear {
         opacity: 0;
      }
      &-appear-active {
         opacity: 1;
         transition: opacity 500ms ease-in;
      }
      &-enter {
         opacity: 0;
      }
      &-enter-active {
         opacity: 1;
         transition: opacity 500ms ease-in;
      }
      &-exit {
         opacity: 1;
      }
      &-exit-active {
         opacity: 0;
         transition: opacity 500ms ease-in;
      }
      &:hover {
         transform: translateY(-5px);
      }
   }
}

@media (max-width: 576px) {
   .comics__grid {
      justify-content: center;
   }
}
